import React from 'react'
import { Services, Slider } from '../components'
import useScrollTop from '../hooks/useScrollTop';
import home_image from '../assets/img/service_image1.jpg';
import useDocTitle from '../hooks/useDocTitle';

const ServicePage = () => {
    useScrollTop();
    useDocTitle("Our services")

    return (
        <div className="bg-gray-50">
            <Slider image={home_image}/>

            <Services/>
            
        </div>
    )
}

export default ServicePage