import React from 'react'
import { Slider, Technology } from '../components'
import useScrollTop from '../hooks/useScrollTop';
import home_image from '../assets/img/home_image1.jpg';
import useDocTitle from '../hooks/useDocTitle';

const HomePage = () => {
    useScrollTop();
    useDocTitle("Home")

    return (
        <div className="bg-gray-50">
            <Slider image={home_image}/>

            <Technology/>

            
        </div>
    )
}

export default HomePage